.fill-modal {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(black, .5);
	display: none; }

.fill-modal__item {
	background-color: #fff;
	border-radius: 10px;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 1000px;
	z-index: 5000;
	text-align: center;
	padding: 50px;
	@include r(1010) {
		width: 90%;
		margin: 0 auto; }
	@include r(768) {
		padding: 20px;
		margin-top: 20px;
		height: 100%;
		overflow-y: scroll;
		border-radius: 10px 10px 0px 0px; } }

.logo-blue_form {
	max-width: 380px;
	height: 80px;
	margin: 0 auto 10px; }

.fill-modal__title {
	color: #0071be;
	font-size: 20px;
	font-weight: 400;
	margin-top: 5px; }

.fill-modal__form {
	width: 43.4%;
	margin: 25px auto;
	text-align: center;
	@include r(768) {
		width: 100%; } }

.form-input {
	border: 1px solid #d0d0d0;
	border-radius: 5px;
	font-weight: 400;
	font-size: 14px;
	width: 100%;
	padding: 11px 15px;
	margin-bottom: 7px;
	color: #999;
	font-family: 'Open Sans', sans-serif;
	&.error {
		box-shadow: 0px 0px 2px 0px red; } }

#confirm-error.error {
	position: absolute;
	bottom: 20px;
	left: 0;
	right: 0;
	margin: 0 auto;
	width: 50%;
	font-weight: 400;
	color: red;
	font-size: 14px;
	border: 1px solid red;
	padding: 5px;
	@include r(1010) {
		width: 80%; }
	@include r(480) {
		width: 100%; } }

.form-btn {
	display: inline-block;
	background-color: $orange;
	font-weight: 400;
	font-family: 'Open Sans', sans-serif;
	font-size: 14px;
	color: #fff;
	border-radius: 7px;
	text-align: center;
	padding: 10.5px 20px;
	margin-top: 14px;
	margin-bottom: 25px; }

.form-confirm {
	display: inline-block; }

.form-confirm-label {
	font-size: 12px;
	font-weight: 400;
	color: #42536a; }

.modal-close {
	position: absolute;
	top: 80px;
	right: 80px;
	width: 20px;
	height: 20px;
	background-image: url("../img/modal-close.png");
	cursor: pointer;
	@include r(850) {
		top: 5px;
		right: 25px; } }

.fill-modal__overlay {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 4999;
	width: 100%;
	height: 100%; }
