.footer {
	padding: 70px 0px 117px;
	text-align: center;
	color: #fff; }

.footer__item-title {
	font-weight: 700;
	font-size: 30px;
	line-height: 40px; }

.logo-white_footer {
	max-width: 300px;
	height: 63px;
	margin: 37px auto; }

.footer__item-descr {
	font-size: 20px;
	font-weight: 400;
	margin-top: -7px;
	line-height: 25px; }

.social {
	text-align: center;
	margin-top: 64px;

	.social-icon {
		display: inline-block;
		height: 80px;
		width: 80px;
		border-radius: 50%;
		position: relative;
		&:nth-child(2) {
			margin: 0px 30px;
			@include r(480) {
				margin: 0px 10px; } }
		a {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			border-radius: 50%; }

		$social: #4867aa; }

	.social-icon_facebook {
		background-color: #4867aa;
		svg {
			font-size: 45px; } }

	.social-icon_linkedin {
		background-color: #3092d1;
		svg {
			font-size: 35px; } }

	.social-icon_vk {
		background-color: #4c75a3;
		svg {
			font-size: 45px; } }

	.social-icon svg {
		fill: #fff;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%); } }
