.header {
	padding: 38px 0px;
	background-image: url("../img/header-bg.png");
	background-position: center center;
	background-repeat: no-repeat;
	min-height: 628px;
	position: relative; }

.logo-white {
	background-image: url("../img/head-logo.svg");
	background-repeat: no-repeat; }

.logo-blue {
	background-image: url("../img/logo.svg");
	background-repeat: no-repeat; }

.navigation {
	position: relative;
	@include clr;
	&.navigation-is-active {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		width: 100%;
		background-color: #fff;
		z-index: 1000;
		padding: 18px 100px;
		transition: all .5s;
		@include r(1530) {
			padding: 18px 50px; }
		@include r(1200) {
			padding: 18px 20px; }
		@include r(480) {
			padding: 10px 0px; } } }

.nav__list {
	float: right;
	@include r(768) {
		float: none;
		text-align: center; }
	@include r(480) {
		margin-left: -15px;
		margin-right: -15px; } }

.nav__list li {
	display: inline-block;
	&:last-child a {
		padding-right: 0px;
		opacity: .5; } }


.nav__list-item {
	padding: 15px 19px;
	font-weight: 700;
	font-size: 14px;
	color: #fff;
	text-decoration: none;
	display: block;
	svg {
		stroke-width: 1px;
		font-size: 26px;
		fill: #fff;
		vertical-align: middle;
		margin-right: 5px;
		margin-top: -5px; }
	&:hover {
		color: $orange; }
	&:hover svg {
		fill: $orange; }
	@include r(480) {
		padding: 15px 5px;
		font-size: 12px; } }


.header__choice {
	position: absolute;
	top: 66%;
	left: 50%;
	transform: translate(-50%, -50%);
	text-align: center;
	@include r(768) {
		top: 80%; }
	@include r(580) {
		width: 100%;
		padding: 0px 15px; } }

.header__choise-title {
	background-image: url("../img/header-choice.png");
	background-repeat: no-repeat;
	width: 549px;
	height: 156px;
	@include r(580) {
		width: 100%;
		background-size: contain;
		background-position: center center; } }

.header__choice-town {
	background-image: url("../img/header-city.png");
	width: 236px;
	height: 233px;
	margin: 0 auto;
	position: relative;
	top: -37px;
	&::before {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;
		z-index: -1;
		box-shadow: 0px 80px 268px 247px #0883c6; } }

.header__choice-btn {
	font-weight: 700;
	color: #fff;
	font-size: 18px;
	padding: 18px 80px;
	background-color: $orange;
	text-decoration: none;
	position: relative;
	top: -20px;
	border-radius: 10px;
	transition: all .5s;
	box-shadow: 3px 3px 0px #1265af;
	&:hover {
		background-color: #fff;
		color: #155da9; }
	@include r(480) {
		padding: 18px 40px; } }

.header__choice-better {
	position: relative;
	top: 23px;
	background-image: url("../img/choice-better.png");
	background-repeat: no-repeat;
	background-position: center center;
	width: 347px;
	height: 222px;
	margin: 0 auto;
	@include r(992) {
		top: 100px;
		width: 100%; } }


.header__choice-better-text {
	font-weight: 600;
	font-size: 30px;
	text-transform: uppercase;
	position: absolute;
	line-height: 40px;
	@include r(992) {
		font-size: 24px; } }

.header__choice-better-text_home {
	color: #7ec8e7;
	text-align: right;
	left: -285px;
	top: 45px;
	svg {
		position: absolute;
		bottom: -50px;
		right: -15px;
		transform: rotate(228deg) rotateY(180deg);
		fill: $orange;
		font-size: 44px;
		@include r(992) {
			transform: rotate(300deg) rotateY(0deg);
			bottom: 0;
			right: auto;
			left: 0; } }
	@include r(992) {
		left: 50%;
		top: -85px;
		transform: translate(-50%);
		text-align: center;
		width: 280px; } }

.header__choice-better-text_present {
	color: #f6ab49;
	right: -239px;
	left: auto;
	top: 81px;
	text-align: left;
	svg {
		position: absolute;
		top: -60px;
		left: 12px;
		transform: rotate(62deg) rotateY(180deg);
		fill: #72bbe0;
		font-size: 44px;
		@include r(992) {
			transform: rotate(120deg) rotateY(0deg);
			top: 0;
			left: auto;
			right: 0; } }
	@include r(992) {
		top: auto;
		bottom: -62px;
		left: 50%;
		right: auto;
		transform: translate(-50%);
		width: 280px;
		text-align: center; } }

.tagline__title {
	font-family: 'UniSans', sans-serif;
	color: $orange;
	font-size: 30px;
	text-transform: uppercase;
	font-weight: 800;
	text-shadow: 1px 1px 0px #145da9;
	display: inline-block;
	letter-spacing: -1px; }

.tagline__descr {
	font-weight: 700;
	color: #145da9;
	font-size: 14px;
	letter-spacing: 3px; }

